.Icon-container {
  cursor             : pointer;
  display            : flex;
  justify-content    : center;
  align-items        : center;
  height             : 48px;
  width              : 48px;
  /* background-color: #CCC; */
}

.Icon-container--check {
  position: absolute;
  left    : 12px;
}

.Icon-container--delete {
  position: absolute;
  top     : -24px;
  right   : 0;
}

.Icon-container--edit {
  position: absolute;
  top     : -24px;
  right   : 48px;
}

.Icon-container--edit svg {
  width : 30px;
  height: 30px;
}

.Icon-svg {
  width     : 24px;
  height    : 24px;
  transition: fill .15s;
}

.Icon-container:hover .Icon-svg--check {
  fill: #4caf50;
}

.Icon-container:hover .Icon-svg--delete {
  fill: red;
}

.Icon-container:hover .Icon-svg--edit {
  fill: #61DAFA;
}